import { Component, OnInit } from '@angular/core';
import { FoodObj } from 'src/app/food/FoodObj';
import { GlobalParamServiceService } from 'src/app/global-param-service.service';

@Component({
  selector: 'app-food',
  templateUrl: './food.component.html',
  styleUrls: ['./food.component.css']
})
export class FoodComponent implements OnInit {
  heading1 = 'FOOD';
  heading2 = 'Explore the Traditional Kerala Food. Enjoy the taste of kerala.';
  foodObjs = [];
  constructor(private globalParamServiceService: GlobalParamServiceService) { }

  ngOnInit() {
    this.globalParamServiceService.emitChange('navbar-shrink');
    this.foodObjs = [
    new FoodObj('Traditional', 'North Indian', 'assets/img/portfolio/Traditional.jpg', '', '', '', ''),
    new FoodObj('Naan', 'North Indian', 'assets/img/portfolio/garlic-naan.jpg', '', '', '', ''),
    new FoodObj('Biryani', 'Indian', 'assets/img/portfolio/Biryani.jpg', '', '', '', ''),
    new FoodObj('Traditional', 'North Indian', 'assets/img/portfolio/Traditional.jpg', '', '', '', ''),
    new FoodObj('Naan', 'North Indian', 'assets/img/portfolio/garlic-naan.jpg', '', '', '', ''),
    new FoodObj('Biryani', 'Indian', 'assets/img/portfolio/Biryani.jpg', '', '', '', '')]
  }

}
