import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from 'src/app/home/home.component';
import { ShoppingComponent } from 'src/app/shopping/shopping.component';
import { FoodComponent } from 'src/app/food/food.component';
import { ContactComponent } from 'src/app/contact/contact.component';
import { WhattovisitComponent } from 'src/app/whattovisit/whattovisit.component';
import { WheretostayComponent } from 'src/app/wheretostay/wheretostay.component';

const routes: Routes = [
 { path: '', component: HomeComponent },
 { path: 'shopping', component: ShoppingComponent },
 { path: 'food', component: FoodComponent },
 { path: 'contact', component: ContactComponent },
 { path: 'whattovisit', component: WhattovisitComponent },
 { path: 'wheretostay', component: WheretostayComponent }
 ];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
